:root {
  --primary-light: #4facfe;
  --primary: #00f2fe;
  --primary-dark: #0a1929;
  --accent: #7b2ff7;
  --accent-secondary: #f107a3;
  --text-dark: #0a1929;
  --text-light: #ffffff;
  --text-muted: #8b9cb6;
  --shadow: rgba(0, 0, 0, 0.3);
  --header-height: 80px;
  --bg-gradient-start: #0a1929;
  --bg-gradient-end: #1a2332;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Inter', 'Segoe UI', system-ui, -apple-system, sans-serif;
}

body {
  background: linear-gradient(180deg, var(--bg-gradient-start) 0%, var(--bg-gradient-end) 100%);
  color: var(--text-light);
  line-height: 1.6;
  padding-top: var(--header-height);
  overflow-x: hidden;
}

.App {
  text-align: left;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
}

header {
  background: rgba(10, 25, 41, 0.8);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  color: white;
  padding: 0;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.3);
  position: fixed;
  width: 100%;
  z-index: 1000;
  top: 0;
  left: 0;
  height: var(--header-height);
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.logo {
  font-size: 1.8rem;
  font-weight: 700;
  color: white;
  text-decoration: none;
  display: flex;
  align-items: center;
  letter-spacing: -0.5px;
}

.logo img, .logo svg {
  height: 55px;
  width: auto;
  margin-right: 12px;
  filter: drop-shadow(0 0 10px rgba(0, 242, 254, 0.3));
  transition: all 0.3s ease;
}

.logo:hover img, .logo:hover svg {
  filter: drop-shadow(0 0 15px rgba(0, 242, 254, 0.5));
}

.logo-fallback {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 55px;
  width: 55px;
  margin-right: 12px;
  background: linear-gradient(135deg, var(--primary) 0%, var(--accent) 100%);
  color: white;
  font-weight: bold;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 242, 254, 0.3);
}

nav ul {
  display: flex;
  list-style: none;
}

nav ul li {
  margin-left: 2rem;
}

nav ul li a {
  color: rgba(255, 255, 255, 0.9);
  text-decoration: none;
  font-weight: 500;
  padding: 0.5rem 1rem;
  border-radius: 8px;
  transition: all 0.3s ease;
  font-size: 0.95rem;
}

nav ul li a:hover {
  background-color: rgba(0, 242, 254, 0.1);
  color: var(--primary);
}

/* Mobile menu button */
.menu-toggle {
  display: none;
  cursor: pointer;
  font-size: 1.8rem;
  color: white;
  padding: 10px;
  -webkit-tap-highlight-color: transparent;
}

.hero {
  background: linear-gradient(180deg, var(--bg-gradient-start) 0%, #0d1b2a 50%, var(--bg-gradient-end) 100%);
  min-height: calc(100vh - var(--header-height));
  display: flex;
  align-items: center;
  color: white;
  position: relative;
  overflow: hidden;
}

.hero::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: radial-gradient(ellipse at 50% 20%, rgba(0, 242, 254, 0.15) 0%, transparent 50%),
              radial-gradient(ellipse at 80% 60%, rgba(123, 47, 247, 0.1) 0%, transparent 50%);
  pointer-events: none;
}

.hero::after {
  content: '';
  position: absolute;
  width: 500px;
  height: 500px;
  background: radial-gradient(circle, rgba(0, 242, 254, 0.2) 0%, transparent 70%);
  border-radius: 50%;
  top: 10%;
  right: -10%;
  animation: pulse 8s infinite ease-in-out;
  filter: blur(80px);
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes pulse {
  0%, 100% { 
    transform: scale(1) translateY(0); 
    opacity: 0.3; 
  }
  50% { 
    transform: scale(1.1) translateY(-20px); 
    opacity: 0.5; 
  }
}

.hero-content {
  position: relative;
  z-index: 10;
  max-width: 800px;
  padding: 4rem 0;
}

.hero h1 {
  font-size: 4.5rem;
  margin-bottom: 1.5rem;
  line-height: 1.1;
  font-weight: 800;
  background: linear-gradient(135deg, #ffffff 0%, rgba(0, 242, 254, 0.9) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  letter-spacing: -2px;
}

.hero p {
  font-size: 1.3rem;
  margin-bottom: 2.5rem;
  opacity: 0.85;
  color: var(--text-muted);
  line-height: 1.8;
  max-width: 700px;
}

.cta-button {
  display: inline-block;
  background: linear-gradient(135deg, var(--primary) 0%, var(--accent) 100%);
  color: white;
  padding: 1rem 2.5rem;
  border-radius: 50px;
  font-weight: 600;
  text-decoration: none;
  font-size: 1.1rem;
  transition: all 0.3s ease;
  box-shadow: 0 10px 30px rgba(0, 242, 254, 0.3);
  position: relative;
  overflow: hidden;
}

.cta-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transition: left 0.5s;
}

.cta-button:hover::before {
  left: 100%;
}

.cta-button:hover {
  transform: translateY(-3px);
  box-shadow: 0 15px 40px rgba(0, 242, 254, 0.4);
}

.section {
  padding: 6rem 0;
  background: var(--bg-gradient-end);
  position: relative;
}

.section-title {
  text-align: center;
  font-size: 3rem;
  margin-bottom: 1.5rem;
  font-weight: 800;
  background: linear-gradient(135deg, #ffffff 0%, var(--primary) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  letter-spacing: -1px;
}

.section-subtitle {
  text-align: center;
  font-size: 1.2rem;
  max-width: 700px;
  margin: 0 auto 4rem;
  color: var(--text-muted);
  line-height: 1.8;
}

.features {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 2rem;
}

/* Collapsible Section Styles */
.collapsible-section {
  margin-bottom: 3rem;
}

.collapsible-header {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 16px;
  padding: 1.5rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-bottom: 1.5rem;
}

.collapsible-header:hover {
  background: rgba(255, 255, 255, 0.08);
  border-color: rgba(0, 242, 254, 0.4);
  transform: translateX(5px);
}

.collapsible-header h3 {
  font-size: 1.8rem;
  font-weight: 700;
  color: white;
  display: flex;
  align-items: center;
  gap: 1rem;
  margin: 0;
}

.collapsible-header h3 i {
  color: var(--primary);
  font-size: 1.6rem;
}

.collapsible-header > i {
  font-size: 1.2rem;
  color: var(--primary);
  transition: transform 0.3s ease;
}

.collapsible-content {
  overflow: hidden;
  transition: max-height 0.5s cubic-bezier(0.4, 0, 0.2, 1), opacity 0.4s ease;
}

.collapsible-content.expanded {
  max-height: 3000px;
  opacity: 1;
}

.collapsible-content.collapsed {
  max-height: 0;
  opacity: 0;
}


.feature-card {
  background: rgba(255, 255, 255, 0.03);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
}

.feature-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, var(--primary) 0%, var(--accent) 100%);
  transform: scaleX(0);
  transition: transform 0.4s ease;
}

.feature-card:hover::before {
  transform: scaleX(1);
}

.feature-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 30px 80px rgba(0, 242, 254, 0.2);
  border-color: rgba(0, 242, 254, 0.3);
}

.feature-icon {
  background: linear-gradient(135deg, rgba(0, 242, 254, 0.1) 0%, rgba(123, 47, 247, 0.1) 100%);
  color: var(--primary);
  font-size: 3rem;
  height: 140px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
}

.feature-icon::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at center, rgba(0, 242, 254, 0.2) 0%, transparent 70%);
  opacity: 0;
  transition: opacity 0.4s ease;
}

.feature-card:hover .feature-icon::after {
  opacity: 1;
}

.feature-content {
  padding: 2rem;
}

.feature-content h3 {
  font-size: 1.6rem;
  margin-bottom: 1rem;
  color: white;
  font-weight: 700;
}

.feature-content p {
  color: var(--text-muted);
  line-height: 1.7;
}

.about-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: center;
}

.about-image {
  background: linear-gradient(135deg, rgba(0, 242, 254, 0.1) 0%, rgba(123, 47, 247, 0.1) 100%);
  border: 1px solid rgba(0, 242, 254, 0.2);
  height: 450px;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.4);
  position: relative;
  overflow: hidden;
}

.about-image::before {
  content: '';
  position: absolute;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, rgba(0, 242, 254, 0.1) 0%, transparent 50%);
  animation: rotate 20s linear infinite;
}

@keyframes rotate {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.about-image img, .about-image svg {
  max-width: 80%;
  max-height: 80%;
  width: 320px;
  height: 320px;
  filter: drop-shadow(0 0 20px rgba(0, 242, 254, 0.3));
  position: relative;
  z-index: 1;
}

.about-text h2 {
  font-size: 3rem;
  margin-bottom: 2rem;
  color: white;
  font-weight: 800;
  letter-spacing: -1px;
}

.about-text p {
  color: var(--text-muted);
  line-height: 1.8;
  margin-bottom: 1.5rem;
}

.contact {
  background: linear-gradient(180deg, var(--bg-gradient-end) 0%, var(--bg-gradient-start) 100%);
  position: relative;
}

.contact::before {
  content: '';
  position: absolute;
  width: 400px;
  height: 400px;
  background: radial-gradient(circle, rgba(123, 47, 247, 0.15) 0%, transparent 70%);
  border-radius: 50%;
  bottom: -20%;
  left: -10%;
  filter: blur(100px);
}

.contact-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
}

.contact-info h2 {
  font-size: 3rem;
  margin-bottom: 2rem;
  color: white;
  font-weight: 800;
  letter-spacing: -1px;
}

.contact-method {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
  padding: 1rem;
  border-radius: 12px;
  transition: all 0.3s ease;
}

.contact-method:hover {
  background: rgba(0, 242, 254, 0.05);
}

.contact-method h4 {
  color: white;
  margin-bottom: 0.3rem;
}

.contact-method p {
  color: var(--text-muted);
}

.contact-icon {
  background: linear-gradient(135deg, var(--primary) 0%, var(--accent) 100%);
  color: white;
  width: 55px;
  height: 55px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 1.2rem;
  font-size: 1.3rem;
  box-shadow: 0 8px 20px rgba(0, 242, 254, 0.3);
}

.contact-form {
  background: rgba(255, 255, 255, 0.03);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  padding: 3rem;
  border-radius: 20px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.4);
}

.form-group {
  margin-bottom: 1.8rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.7rem;
  font-weight: 600;
  color: white;
  font-size: 0.95rem;
}

.form-control {
  width: 100%;
  padding: 1rem 1.2rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  font-size: 1rem;
  transition: all 0.3s ease;
  background: rgba(255, 255, 255, 0.05);
  color: white;
}

.form-control::placeholder {
  color: var(--text-muted);
}

.form-control:focus {
  outline: none;
  border-color: var(--primary);
  background: rgba(255, 255, 255, 0.08);
  box-shadow: 0 0 0 3px rgba(0, 242, 254, 0.1);
}

textarea.form-control {
  resize: vertical;
  min-height: 140px;
}

.submit-btn {
  background: linear-gradient(135deg, var(--primary) 0%, var(--accent) 100%);
  color: white;
  border: none;
  padding: 1.1rem 2rem;
  border-radius: 50px;
  font-size: 1.05rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  width: 100%;
  box-shadow: 0 10px 30px rgba(0, 242, 254, 0.3);
  position: relative;
  overflow: hidden;
}

.submit-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transition: left 0.5s;
}

.submit-btn:hover::before {
  left: 100%;
}

.submit-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 15px 40px rgba(0, 242, 254, 0.4);
}

footer {
  background: var(--bg-gradient-start);
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  color: white;
  padding: 4rem 0 2rem;
}

.footer-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 3rem;
}

.footer-column h3 {
  font-size: 1.3rem;
  margin-bottom: 1.8rem;
  position: relative;
  padding-bottom: 0.8rem;
  font-weight: 700;
  color: white;
}

.footer-column h3::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 50px;
  height: 3px;
  background: linear-gradient(90deg, var(--primary) 0%, var(--accent) 100%);
  border-radius: 2px;
}

.footer-column p {
  color: var(--text-muted);
  line-height: 1.7;
}

.footer-column ul {
  list-style: none;
}

.footer-column ul li {
  margin-bottom: 0.8rem;
}

.footer-column ul li a {
  color: var(--text-muted);
  text-decoration: none;
  transition: all 0.3s ease;
  display: inline-block;
}

.footer-column ul li a:hover {
  color: var(--primary);
  transform: translateX(5px);
}

.social-icons {
  display: flex;
  gap: 1rem;
  margin-top: 1.5rem;
}

.social-icon {
  background: rgba(0, 242, 254, 0.1);
  border: 1px solid rgba(0, 242, 254, 0.2);
  width: 45px;
  height: 45px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  color: var(--primary);
}

.social-icon:hover {
  background: linear-gradient(135deg, var(--primary) 0%, var(--accent) 100%);
  border-color: transparent;
  color: white;
  transform: translateY(-3px);
  box-shadow: 0 8px 20px rgba(0, 242, 254, 0.3);
}

.footer-bottom {
  margin-top: 3rem;
  text-align: center;
  padding-top: 2rem;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  color: var(--text-muted);
}

@media (max-width: 992px) {
  :root {
    --header-height: 70px;
  }
  
  .hero h1 {
    font-size: 3.5rem;
  }
  
  .section-title {
    font-size: 2.5rem;
  }
  
  .about-content, .contact-container {
    grid-template-columns: 1fr;
  }
  
  .about-image {
    height: 350px;
  }
}

@media (max-width: 768px) {
  :root {
    --header-height: 70px;
  }
  
  .header-content {
    padding: 0 1rem;
  }

  .logo {
    font-size: 1.5rem;
  }

  .logo img, .logo svg {
    height: 45px;
  }
  
  .menu-toggle {
    display: block;
  }

  nav {
    position: fixed;
    top: var(--header-height);
    left: 0;
    width: 100%;
    background: rgba(10, 25, 41, 0.98);
    backdrop-filter: blur(20px);
    padding: 1rem 0;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
    transform: translateY(-100%);
    opacity: 0;
    transition: all 0.3s ease;
    z-index: 999;
  }
  
  nav.active {
    transform: translateY(0);
    opacity: 1;
  }
  
  nav ul {
    flex-direction: column;
    align-items: center;
  }
  
  nav ul li {
    margin: 0.5rem 0;
    width: 100%;
    text-align: center;
  }
  
  nav ul li a {
    display: block;
    padding: 1rem;
    font-size: 1.1rem;
  }
  
  .hero h1 {
    font-size: 2.5rem;
    letter-spacing: -1px;
  }
  
  .hero p {
    font-size: 1.1rem;
  }
  
  .section-title {
    font-size: 2.2rem;
  }
  
  .section-subtitle {
    font-size: 1.1rem;
  }
  
  .about-text h2, .contact-info h2 {
    font-size: 2.2rem;
  }
  
  .contact-container {
    gap: 3rem;
  }
  
  .contact-form {
    padding: 2rem;
  }
  
  .features {
    grid-template-columns: 1fr;
  }
}

/* Fix for iPhone notch */
@supports (padding: max(0px)) {
  header {
    padding-left: max(2rem, env(safe-area-inset-left));
    padding-right: max(2rem, env(safe-area-inset-right));
    padding-top: max(0px, env(safe-area-inset-top));
  }
  
  .container {
    padding-left: max(2rem, env(safe-area-inset-left));
    padding-right: max(2rem, env(safe-area-inset-right));
  }
}
